*{
    box-sizing: border-box;
    margin: 0;
    border: 0;
}
.active-day{
    background-color: #f62626bc;
}
.active-day span{
    color: #fff;
}
.each-day{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    cursor: pointer;
    border-radius: 5px;
}
.each-day:hover{
    color: #f62626bc;
}
.each-day span{
    line-height: 18px !important;
}
.each-day .months{
    font-size: 13px;
}
.each-day .day{
    font-size: 14px;
}

.shows-hide{
    display: none !important;
}



.fav i{
    font-size: 13px;
}
.fav-show{
    color: rgb(238, 18, 18);
    font-size: 16px;
    cursor: pointer;
    visibility: hidden;
}
.fav-hide{
    cursor: pointer;
    z-index: 999;
}
.fav-hide:hover .fav-show{
    visibility: visible !important;
}
.t-name{
    font-size: 13px;
}
.t-name:hover{
    text-decoration: underline;
}
.name p{
    font-size: 14px;
}
.online-book span , .food span{
    font-size: 20px;
}
.online-book p , .food p {
    font-size: 12px;
}
.dangers{
    font-size: 16px;
    color: #444;
}

.cancel-status{
    display: flex;
    justify-content: center;
    align-items: center;
}


/* ======== show timeing ========== */
.list-theater{
    background: rgb(122 115 115 / 9%);
    padding: 10px 13px;
}
.listOf{
    overflow: hidden;
}
.show-time{
    width:100px;
    cursor: pointer;
}
.show-time .medium{
    font-size: 14px;
}
.show-time .small{
    font-size: 10px;
}
.cancel i{
    font-size: 32px;
}
.cancel span{
    font-size: 12px;
}


/* ========== panel section ======== */
.panel{
    background: #353842;
}
.certificate{
    width: 20px;
    height: 20px;
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
}
.genre{
    font-size: 11px;
    width:fit-content;
    border: 1px solid;
    height: 16px;
    padding: 0 7px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    color: rgba(255, 255, 255, 0.5);
}


/* ==== pop up ==== */

.pop-btn{
   background: #f84464;
   color: #fff;
   font-size: 15px;
}
.seat-img{
    height:200px;
}
.seat-img img{
    object-fit: cover;
}
.pop-details .print{
    text-transform: uppercase;
    font-size: 13px;
    margin-bottom: 1px !important;
    color: rgb(112, 105, 105);
}
.pop-details .hire{
    font-size: 12px;
    margin-bottom: 1px !important;
    color: rgb(24, 23, 23);
    font-weight: bold;
}
.pop-details .spans{
    font-size: 11px;
    color: rgb(59, 228, 50);
    font-weight: 300;
}
.seat-num:hover{
    background: #f84464;
    color: #fff !important;
}
.active{
    background: #f84464;
    color: #fff !important;
}
.seat-num{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    cursor: pointer;
    transition: all .5s linear;
}