.foot-two{
    background: rgba(0, 0, 0, 0.769);
}
.f-icon{
    font-size: 40px;
}
.f-content{
    font-size: 13px;
}
.f-icons:hover{
    color: #fff !important;
}
.movie-link,.line{
    font-size: 12px;
    color:rgb(127, 127, 127);
}
.movie-link:hover{
    color:#fff;
}
.f-movie{
    font-size: 12px;
    color:rgb(165, 165, 165);
}
.f-social{
    display: flex;
    justify-content: center;
    align-items: center;
    gap:6px;
}
.f-social a{
    background: rgb(127, 127 ,127);
    width: 30px;
    height: 30px;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    transition: all .3s linear;
}

.f-social a:hover{
    background: #fff;
}
.f-copy p{
    font-size: 10px;
    color:rgb(127, 127 ,127);
    line-height: 0 !important;
}
.f-logo{
    position: relative;
}
.f-logo:after {
    position: absolute;
    content: "";
    height: 1px;
    background-color: #8a8989;
    width: 50%;
    top: 50%;
    right: 0;
} 
.f-logo:before {
    position: absolute;
    content: "";
    height: 1px;
    background-color: #8a8989;
    width: 50%;
    left:0;
    top: 50%;
} 
.social-i i{
    color: rgb(33, 30, 30);
}