@media (max-width:768px) {
    .post-main {
        background: #fff;
    }

    .fixed-btn {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 999;
    }
}

@media (min-width:768px) {
    .post-main {
        background: rgb(26, 26, 26);
    }

    .hide {
        background: #fff;
        box-shadow: rgba(0, 0, 0, 0.16) 0 1px 8px 0;
        position: sticky;
        top: 0;
        display: none;
    }

}

.bg-images {
    min-height: 460px;
    max-width: 1440px;
    margin: 0 auto;
    width: 92%;
    padding: 28px 0;
    background-color: rgb(26, 26, 26);
}

.post-inner {
    max-width: 1240px;
    margin: 0 auto;
}

/* =====left======= */
.post-left {
    border-radius: 6px 6px 0 0;
}

.img-post img {
    border-radius: 6px 6px 0 0;
}

.post-left .bottom {
    background: rgb(2, 2, 2);
    border-radius: 0 0 6px 6px;
    padding: 2px;
}

.post-left .bottom span {
    font-size: 12px;
    font-weight: bold;
}


/* ======== mid ====== */
.rating-star {
    color: rgb(246, 114, 128);
}

.vote-link {
    cursor: pointer;
}

.rt-ar {
    font-size: 14px;
    vertical-align: bottom;
}

.rate-now {
    background: rgb(51 51 51);
    padding: 12px 24px;
    border-radius: 6px;
    cursor: pointer;
}

.rate-text h6 {
    font-size: 18px;
    line-height: 24px !important;
    margin-block-end: 0 !important;
}

.rate-text p {
    font-size: 16px;
    line-height: 24px !important;
    margin-block-end: 0 !important;
    color: rgb(204, 204, 204)
}

.book-btn {
    background: rgba(248, 68, 100);
    color: #fff;
}

.t-c {
    padding: 3px 9px;
    background: rgb(248, 247, 247);
    border-radius: 3px;
    cursor: pointer;
}

.t-c span {
    font-size: 16px;
    font-weight: 500;
}

.t-c:hover span {
    text-decoration: underline;
}

.all-detail {
    display: flex;
    color: rgb(255, 255, 255);
    font-size: 16px;
    line-height: 24px;
}

.all-detail span {
    padding: 0 8px;
    line-height: 24px;
    font-size: 16px;
}

.all-details {
    display: flex;
    color: rgb(16, 16, 16);
    font-size: 16px;
    line-height: 24px;
}

.all-details span {
    padding: 0 8px;
    line-height: 24px;
    font-size: 16px;
}

/* ======== right =========== */
.share-btn {
    background: rgba(34, 34, 34, 0.8);
    height: 48px;
    cursor: pointer;

}

.share-btn span {
    font-size: 30px;
}

.share-btn p {
    line-height: 20px !important;
}


/* ============== hide section ========= */

.show-hide {
    display: block !important;
}

.hide-rate {
    font-weight: 500;
}

.hide-vote {
    font-size: 12px;
}

.hide-btn {
    background: rgba(248, 68, 100);
    color: #fff;
    width: 100%;
    padding: 7px 9px;
    border-radius: 5px;
}

/* =========== about-movie= ========== */

.movie-abt h3 {
    font-size: 20px;
    font-weight: bolder;
    text-align: left;
}

.movie-abt p {
    font-size: 14px;
    text-align: left;
    color: rgb(85, 82, 82);
}


/* ======== cast ========= */

.img-col {
    width: 120px;
    height: 120px;
    border-radius: 120px;
}

.img-col img {
    width: 100%;
    object-fit: cover;
    border-radius: inherit;
}

.cast h3 {
    font-size: 20px;
    font-weight: bolder;
    text-align: left;
    /* margin-bottom: 10px; */
}

.cols {
    width: 120px;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.cols h5 {
    font-size: 15px;
    font-weight: 600;
}

.cols span {
    font-size: 13px;
    color: #444;
}

.bottom-booK {
    opacity: 99;
}