
.banner{
    background: rgb(235, 229, 229);
}
.ban-img img{
    width: 100%;
    object-fit: cover;
    border-radius: 5px;
}
.ban-img{
    padding: 0 5px;
}
.slick-next:before{
    color:#ffffff !important;
    position: absolute;
    right:15px;
    background: #000000cf;
    width: 35px;
    height: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.slick-prev:before{
    color:#ffffff !important;
    position: absolute;
    left:13px;
    background: #000000cf;
    width: 35px;
    height: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.slick-prev{
    z-index:1;
}