
a{
    text-decoration: none;
}

@media (max-width:768px){
    .user-search input{
        width: 80%;
        font-size: 10px;
    }
    .ban-img{
        height: 100px;
    }
    .ban-img img{
        height: inherit;
    }
}
@media (min-width:768px){
    .user-search input{
        width: 90%;
        font-size: 13px;
    }
}
/* =====  nav  ===== */
#navi{
    background:#353842;
}
.user-icons{
    cursor: pointer;
}
.logo img{
    width:100%;
    object-fit: cover;
}
.user-search input:focus{
    outline: none !important;
}
.nav-right{
    gap:10px;
}
.nav-right a,.nav-right a span,.nav-right button{
    font-size: 14px;
}
.nav-right a:hover{
    color: #fff !important;
}
.list-icon i{
    font-size: 18px;
}


#links{
    background: rgb(31, 37, 51);
}
.links-lists a{
    font-size: 14px;
    color: #787b83;
}
.links-lists a:hover{
    color:#fff;
}
.links-list a{
    color:#fff;
    font-size: 13px;
}
.side-menu{
    cursor: pointer;
}

.dp{
    cursor: pointer;
    font-size: 30px;
}
.user-profile{
    width: 30px;
    border-radius: 50%;
    cursor: pointer;
}