*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
   .cols{
        padding:0 10px;
    }
    .cols .toShow{
        width:100%;
        object-fit: cover;
    }
    .movie-genres{
        font-size: 14px;
        color:rgb(82, 78, 78);
    }
    .showAll{
        font-size: 13px;
    }
    .all-arrow{
        font-size:13px;
    }
    
    
    .cols{
        cursor: pointer;
    }
    .slide-btn{
        width:25px;
        height:25px;
        border-radius: 25px;
    }

