*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

@media (max-width:992px){
    .seats-show{
       overflow-x: auto;
    }
}

.seat-top{
    background: #1f2533;
}
.total-seats{
    margin-bottom: 0 !important;
    font-size: 12px;
}
.seat-edit{
    font-size: 12px;
}
.edit-div{
    padding:2px 6px;
    border-radius: 4px;
    cursor: pointer;
}
.seats-cancel{
    font-size: 14px;
    cursor: pointer;
}
.cancelseats{
    font-size: 19px;
    cursor: pointer;
}
.seat-title{
    font-size: 14px;
}
.seat-cer{
    width: 20px;
    height: 20px;
    border-radius: 20px;
    border: .7px solid #fff;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.seats-title{
    font-size: 13px;
    color: #c6c6c9;
    font-weight: 600;
}







.seats-show{
    width: 100%;
}
.one-seat{
    width:30px;
    height:30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 7px;
}
.select-seat{
    width:30px;
    height:30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 7px;
}
.one-sea{
    width:30px;
    height:30px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.one-seat:hover{
    background: rgb(67, 240, 95);
    color: #fff;
}
.disabled{
    background: #444;
}
.selected{
    background: rgb(67, 240, 95);
}
.none{
    visibility: hidden;
}
.inner-container-seat{
    width: 80%;
    margin: 0 auto;
}
.firstRow{
    display: flex;
    gap: 4px;
}
.firstA{
    display: flex;
    gap: 4px;
}
.alter-seats{
    display: flex;
    gap: 4px;
}
.all-alter{
    display: flex;
    gap: 4px;
}
.alter-side{
    display: flex;
    gap: 4px;
}


.demo-seat{
    width: 15px;
    height: 15px;
}
.seat-category{
    font-size: 13px;
}

.amount-show{
    box-shadow: 0 0 10px 0 rgba(0,0,0,.5);
    position: fixed;
    bottom: 0;
    background: #fff;
    display: none;
}
.amount-btn{
    padding: 4px 7px;
    background: #f84464;
    color: #fff;
    border-radius: 5px;
}
.payment{
    display: block;
}



.booked{
    background: rgb(120, 117, 117);
}