*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}


.food-link{
    width: fit-content;
    height: 20px;
    border-radius: 10px;
    border: .4px solid rgb(142, 137, 137);
    padding: 2px 8px;
    display: flex;
    align-items: center;
    font-size: 10px;
    text-transform: uppercase;
    color: #000;
}
.actives{
    background: #ed5873;
    color: #fff;
}
.heading-food{
    letter-spacing: 2px;
}
.heading-food span{
    color: #dd193d;
}
.food-booking{
    color: rgb(118, 115, 115);
    font-size: 13px;
}

.food-name{
    font-size:15px;
    font-weight: 500;
}
.food-type{
    font-size: 12px;
    color: #817676;
}
.food-rate{
    font-size: 14px;
    color: rgb(10, 9, 9);
    font-weight: 600;
}
.food-add{
    border: 1px solid #db2525b5;
    padding: 2px 10px;
    background: none;
    color: #db2525b5;
    border-radius: 5px;
    font-size: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.book-right-head{
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #bf4d62;
    text-align: left;
    font-size: 13px;
    word-spacing: 3px;
}
.right-numbers{
    font-size: 14px;
    color: #000;
    font-weight: 600;
}
.seats-numbers-final{
    font-size: 13px;
    color: #000;
    font-weight: 600;
}
.totals{
    font-size: 14px;
}
.final-count{
    font-size: 12px;
}
.fee{
    margin-bottom: 0 !important;
}
.convinence{
    border-bottom: 1px dotted #444;
}
.show-more{
    font-size: 13px;
    width: 13px;
    height: 13px;
    border-radius: 13px;
    border: .5px solid #444;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.sub{
    font-size: 14px;
    font-weight: 600;
    color: #000;
}



.pay-btn{
    background: #c7415ac8;
    border-radius: 4px;
    padding: 5px 7px;
    cursor: pointer;
}

/* pop up ============ */

.text-pay{
    color:rgb(0, 0, 0);
}
.pay-modal{
    background: rgb(91, 91, 226);
}
.user-inp{
    font-size: 13px;
}
.ruppe{
    font-size: 18px;
    font-weight: 600;
}
.pays-btn{
    background: rgb(51, 219, 51);
    padding: 5px 7px;
    border-radius: 7px;
    font-size: 15px;
}